/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase, capitalCase } from 'change-case';
import { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';

// @mui
import { useTheme } from '@mui/material/styles';
import {
    Card,
    Table,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    Switch,
    Tabs,
    Button,
    Tab,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    Stack
} from '@mui/material';

// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// format number
import { fCurrency } from '../../utils/formatNumber';
// sections
import { TransHead, PlanToolbar } from '../../sections/admin/user/list';
// axios
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
    { id: 'referrence', label: 'Reference', alignRight: false },
    { id: 'type', label: 'Type', alignRight: false },
    { id: 'details', label: 'Details', alignRight: false },
    { id: 'phone', label: 'Phone/Account', alignRight: false },
    { id: 'amount', label: 'Amount', alignRight: false },
    { id: 'oldbal', label: 'Old Bal', alignRight: false },
    { id: 'newbal', label: 'New Bal', alignRight: false },
    { id: 'date', label: 'Date', alignRight: false },
    { id: 'status', label: 'Status', alignRight: false },
    { id: 'action', label: 'Action', alignRight: false },
];

const STATUS_TABS = [
    { value: 'ALL', label: 'ALL' },
    { value: 1, label: 'Success' },
    { value: 0, label: 'Processing' },
    { value: 2, label: 'Failed' }
];

const TRANSACTION_TYPES = [
    { value: 'all', label: 'All Transactions', endpoint: 'history' },
    { value: 'data', label: 'Data', endpoint: 'datatrans' },
    { value: 'airtime', label: 'Airtime', endpoint: 'airtime' },
    { value: 'cable', label: 'Cable', endpoint: 'cable' },
    { value: 'bill', label: 'Electricity', endpoint: 'bill' },
    { value: 'bulksms', label: 'Bulk SMS', endpoint: 'bulksms' },
    { value: 'stock', label: 'Stock', endpoint: 'stock' },
    { value: 'airtimecash', label: 'Airtime 2 Cash', endpoint: 'airtimecash' },
    { value: 'result', label: 'Result Checker', endpoint: 'result' },
    { value: 'data_card', label: 'Data Card', endpoint: 'data_card' },
    { value: 'recharge_card', label: 'Recharge Card', endpoint: 'recharge_card' }
];

// ----------------------------------------------------------------------

export default function Transactions() {
    const theme = useTheme();
    const { themeStretch } = useSettings();
    const [userList, setUserList] = useState([]);
    const [page, setPage] = useState(0);
    const [load, SetLoad] = useState(true);
    const [totalPage, SetTotal] = useState(0);
    const [filterName, setFilterName] = useState('');
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [dense, Setdense] = useState(false);
    const [isNotFound, SetNotFound] = useState(false);
    const [status, setStatus] = useState('ALL');
    const [transactionType, setTransactionType] = useState('all');
    const AccessToken = window.localStorage.getItem('accessToken');

    useEffect(() => {
        initialize(page, rowsPerPage, status, filterName, transactionType);
    }, []);

    const initialize = async (pag, Habukhan = 10, statusFilter, search, type) => {
        let Habukhan_page;
        if (pag > page) {
            Habukhan_page = pag + 1;
        } else {
            Habukhan_page = pag;
        }
        SetLoad(true);

        try {
            let response;
            let transData;

            // Use correct endpoints based on transaction type
            if (type === 'all') {
                response = await axios.get(
                    `/api/system/all/history/records/${AccessToken}/secure?page=${Habukhan_page}&limit=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.all_summary;
            } else if (type === 'data') {
                response = await axios.get(
                    `/api/system/all/datatrans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.data_trans;
            } else if (type === 'airtime') {
                response = await axios.get(
                    `/api/system/all/airtime/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.airtime_trans;
            } else if (type === 'cable') {
                response = await axios.get(
                    `/api/system/all/cable/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.cable_trans;
            } else if (type === 'bill') {
                response = await axios.get(
                    `/api/system/all/bill/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.bill_trans;
            } else if (type === 'bulksms') {
                response = await axios.get(
                    `/api/system/all/bulksms/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.bulksms_trans;
            } else if (type === 'stock') {
                response = await axios.get(
                    `/api/system/all/stock/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.stock_trans;
            } else if (type === 'airtimecash') {
                response = await axios.get(
                    `/api/system/all/airtimecash/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.airtimecash_trans;
            } else if (type === 'result') {
                response = await axios.get(
                    `/api/system/all/result/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.result_trans;
            } else if (type === 'data_card') {
                response = await axios.get(
                    `/api/system/all/data_card/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.data_card_trans;
            } else if (type === 'recharge_card') {
                response = await axios.get(
                    `/api/system/all/recharge_card/trans/system/${AccessToken}/secure?page=${Habukhan_page}&Habukhan=${Habukhan}&status=${statusFilter}&search=${search}`
                );
                transData = response.data?.recharge_card_trans;
            }

            setUserList(transData?.data || []);
            SetTotal(transData?.total || 0);
            SetLoad(false);
            setPage(pag);
            SetNotFound((transData?.total || 0) === 0);
        } catch (error) {
            console.error('Error fetching transactions:', error);
            SetLoad(false);
            setUserList([]);
            SetNotFound(true);
        }
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        initialize(0, event.target.value, status, filterName, transactionType);
        setPage(0);
    };

    const handleFilterByName = async (filterName) => {
        setFilterName(filterName);
        setPage(0);
        initialize(page, rowsPerPage, status, filterName, transactionType);
    };

    const handleTransactionTypeChange = (event) => {
        const newType = event.target.value;
        setTransactionType(newType);
        setPage(0);
        initialize(0, rowsPerPage, status, filterName, newType);
    };

    function handleChangeDense(event) {
        Setdense(event.target.checked);
    }

    return (
        <Page title="Transactions">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Transactions"
                    links={[
                        { name: 'Dashboard', href: PATH_DASHBOARD.general.app },
                        { name: 'Transactions' },
                    ]}
                />

                <Card>
                    <Stack spacing={2} sx={{ p: 3, pb: 0 }}>
                        <FormControl fullWidth>
                            <InputLabel>Transaction Type</InputLabel>
                            <Select
                                value={transactionType}
                                label="Transaction Type"
                                onChange={handleTransactionTypeChange}
                            >
                                {TRANSACTION_TYPES.map((type) => (
                                    <MenuItem key={type.value} value={type.value}>
                                        {type.label}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Stack>

                    <Tabs
                        value={status}
                        scrollButtons="auto"
                        variant="scrollable"
                        allowScrollButtonsMobile
                        onChange={(e, value) => {
                            setStatus(value);
                            initialize(page, rowsPerPage, value, filterName, transactionType);
                        }}
                    >
                        {STATUS_TABS.map((tab) => (
                            <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
                        ))}
                    </Tabs>

                    <PlanToolbar filterName={filterName} onFilterName={handleFilterByName} />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: 800 }}>
                            <Table size={dense ? 'small' : 'medium'}>
                                <TransHead
                                    order={'asc'}
                                    orderBy={'id'}
                                    headLabel={TABLE_HEAD}
                                    rowCount={userList.length}
                                />

                                {!load ? (
                                    <TableBody>
                                        {userList.map((row, index) => {
                                            const {
                                                transid,
                                                plan_phone,
                                                amount,
                                                oldbal,
                                                newbal,
                                                plan_date,
                                                plan_status,
                                                network,
                                                plan_name,
                                                network_type
                                            } = row;

                                            return (
                                                <TableRow hover key={transid || index} tabIndex={-1} role="checkbox">
                                                    <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                                                        <Typography variant="subtitle2" noWrap>
                                                            {transid}
                                                        </Typography>
                                                    </TableCell>
                                                    <TableCell align="left">{TRANSACTION_TYPES.find(t => t.value === transactionType)?.label || 'N/A'}</TableCell>
                                                    <TableCell align="left">
                                                        {network && <>{network} - </>}
                                                        {network_type && <>{network_type} - </>}
                                                        {plan_name}
                                                    </TableCell>
                                                    <TableCell align="left">{plan_phone}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(amount)}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(oldbal)}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(newbal)}</TableCell>
                                                    <TableCell align="left">{plan_date}</TableCell>
                                                    <TableCell align="left">
                                                        <Label
                                                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                                                            color={plan_status === 1 ? 'success' : plan_status === 0 ? 'warning' : 'error'}
                                                        >
                                                            {sentenceCase(
                                                                (plan_status === 0 && 'processing') ||
                                                                (plan_status === 1 && 'successful') ||
                                                                (plan_status === 2 && 'failed')
                                                            )}
                                                        </Label>
                                                    </TableCell>
                                                    <TableCell align="left">
                                                        <Button
                                                            variant="contained"
                                                            size="small"
                                                            component={RouterLink}
                                                            to={`${PATH_DASHBOARD.general.invioce}/${transid}/${transactionType}`}
                                                        >
                                                            Invoice
                                                        </Button>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                    </TableBody>
                                ) : (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={10} sx={{ py: 3 }}>
                                                <Typography variant="body2">Loading ...</Typography>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}

                                {isNotFound && (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={10} sx={{ py: 3 }}>
                                                <SearchNotFound searchQuery={filterName} />
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <FormControlLabel
                        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
                        label="Dense padding"
                        align="left"
                    />

                    <TablePagination
                        rowsPerPageOptions={[5, 10, 25, 100]}
                        component="div"
                        count={totalPage}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={(e, page) => initialize(page, rowsPerPage, status, filterName, transactionType)}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </Card>
            </Container>
        </Page>
    );
}
